import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectPoItemMasterComponent } from './direct-po-item-master.component';
import { CommonModule } from '@angular/common';
const routes: Routes = [
    {
        path: '', 
        component: DirectPoItemMasterComponent,
       
    }
  ];
  
  @NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })

export class DirectPoItemMasterRoutingModule{}


