import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DirectPoItemMasterRoutingModule } from './direct-po-item-master-routing.module';
import { DirectPoItemMasterComponent } from './direct-po-item-master.component';


@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DirectPoItemMasterRoutingModule,
  ],
  declarations: [
    DirectPoItemMasterComponent
  ],
  schemas:[NO_ERRORS_SCHEMA]
})
export class DirectPoItemMasterModule { }



